.nav-bar {
    background-color: #3a3a3a;
    border: solid 1px rgba(85, 85, 85, 0.5);
	min-height: 6vh;
}

h2 {
    margin:0;
    line-height: 41px;
}

.nav-bar button {
    -webkit-appearance: none;
    margin: 0 8px;
	background-color: #59ab80;
	border: none;
	border-radius: 5rem;
	box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
    color: #5a420b;
	cursor: pointer;
	font: 0.8em Raleway, sans-serif;
	letter-spacing: .05em;
	outline: none;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
    min-width: 4rem;
    padding: 6px;
    font-weight: 600;
}

.nav-bar button:hover {
	background-color: #bd7e7e;
}